import './App.css';
import './metro-dist/css/metro-responsive.min.css';
import './metro-dist/css/metro.min.css';
import './metro-dist/js/metro.js';
import './metro-dist/css/metro-icons.min.css';
import './metro-dist/css/metro-schemes.min.css';

import * as React from 'react';

import Header from './components/Header';
import Options, { OptionsParams } from './components/Options';
import TextBox from './components/Textbox';

export default class App extends React.Component {
    private textbox: TextBox;
    handleOptionsChange = (op: OptionsParams) => {
        this.textbox.setState({ optionsParams: op });
    };

    render() {
        return (
            <div>
                <Header />
                <div className="container page-content flex-grid flex-dir-row-reverse">
                    <div className="workspace row cells2 ">
                        <TextBox
                            ref={tb => {
                                this.textbox = tb as TextBox;
                            }}
                        />
                        <Options onChange={this.handleOptionsChange} />
                    </div>
                </div>
            </div>
        );
    }
}



